:root {
  --font-roboto: "Roboto", sans-serif;
  --font-playfair: "Playfair Display", serif;
}

:root {
  --blue-grey: #e0e7ef;
  --v-light-grey: #f3f3f3;
  --light-grey: #E5E5E5;
  --med-grey: #4d4d4d;
  --v-dark-grey: #1a1a1a;
  --white: #fff;
  --black: #000;
  --light-orange: #e5955b;
  --orange: #d38449;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-roboto);
}
body.noscroll {
  overflow: hidden;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  margin: 0;
  line-height: 1;
  font-weight: normal;
}

h1,
h2,
h3 {
  font-family: var(--font-playfair);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.375rem;
  max-width: 67.25rem;
  margin: 0 auto;
}
@media (min-width: 48.125em) {
  .container {
    padding: 0 5.625rem;
  }
}
@media (max-width: 48.0625em) {
  .container {
    padding: 0 5.625rem;
  }
}
@media (max-width: 32.5em) {
  .container {
    padding: 0 1.375rem;
  }
}

.header {
  background: url(img/header-dog.jpeg) no-repeat center;
  background-size: cover;
  background-position: 50% 50%;
  height: 28.875rem;
  text-align: center;
}
.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 1.375rem;
}
.header .nav .logo {
  width: 1.875rem;
}
.header .nav .menu-mobile {
  display: block;
  height: 100vh;
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1001;
  opacity: 0;
  visibility: hidden;
}
@media (min-width: 48.125em) {
  .header .nav .menu-mobile {
    visibility: hidden;
    opacity: 0;
  }
}
.header .nav .menu-mobile.ativo {
  width: 60%;
  visibility: visible;
  opacity: 1;
}
@media (min-width: 48.125em) {
  .header .nav .menu-mobile.ativo {
    width: 30%;
  }
}
.header .nav .menu-mobile ul {
  text-align: center;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
}
.header .nav .menu-mobile ul li:not(:last-child) {
  width: 100%;
}
.header .nav .menu-mobile ul a:not(.call) {
  display: block;
  color: var(--white);
  display: block;
  font-size: 1.25rem;
  padding: 0.5rem 0;
  margin-bottom: 0.625rem;
  transition: 0.3s ease-in-out;
}
.header .nav .menu-mobile ul a:not(.call):hover {
  background: var(--orange);
}
.header .nav .menu-mobile ul .call {
  display: block;
  background: var(--orange);
  border-radius: 1.875rem;
  color: var(--white);
  padding: 0.625rem 1.25rem;
}
.header .nav .menu-mobile ul .call:hover {
  background: var(--light-orange);
}
.header .nav .icon_close-menu {
  height: 1.5rem;
  width: 1.5rem;
  margin: 1.5625rem 1.25rem 1.25rem auto;
}
.header .nav .menu-desk ul {
  display: flex;
}
.header .nav .menu-desk ul a {
  font-size: 1rem;
  color: var(--med-grey);
  font-weight: 500;
  transition: 0.3s ease-in-out;
}
.header .nav .menu-desk ul a:not(.call) {
  margin-right: 1.25rem;
  padding: 0.625rem 0.9375rem;
  border-bottom: 3px solid transparent;
}
.header .nav .menu-desk ul a:not(.call):hover {
  border-color: #d38449;
}
.header .nav .menu-desk ul .call {
  background: var(--orange);
  border-radius: 1.875rem;
  color: var(--white);
  padding: 0.625rem 1.25rem;
}
.header .nav .menu-desk ul .call:hover {
  background: var(--light-orange);
}
.header .overlay {
  display: block;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
}
@media (min-width: 48.125em) {
  .header .overlay {
    visibility: hidden;
    opacity: 0;
  }
}
.header h2 {
  font-size: 1.25rem;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.625rem;
  line-height: 1;
  margin: 2.5rem 0 1.25rem;
}
.header h1 {
  line-height: 1;
  font-size: 3.75rem;
  font-weight: 700;
}
@media (min-width: 61.875em) {
  .header {
    height: 100vh;
  }
}

@media (max-width: 61.8125em) {
  .hide-mobile {
    display: none;
  }
}

@media (min-width: 61.875em) {
  .hide-desk {
    display: none;
  }
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
    margin-left: -100%;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
    margin-left: 0;
  }
}
.fade-in {
  animation: fade-in 500ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    margin-left: 0;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    margin-left: -100%;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 500ms ease-in-out forwards;
}

.apresentacao {
  display: grid;
  grid-template-columns: 1fr;
  place-content: center;
  margin-top: 3.75rem;
  margin-bottom: 3.75rem;
}
.apresentacao .apresentacao__foto--dupla {
  height: 330px;
  background: url(../../../img/dog-man.jpeg) no-repeat right;
  background-size: auto;
  background-size: cover;
}
.apresentacao .apresentacao__foto--dog {
  height: 330px;
  background: url(../../../img/doggie.jpeg) no-repeat center;
  background-size: auto;
  background-size: cover;
}
.apresentacao .apresentacao__call {
  color: var(--white);
  display: grid;
  place-content: end;
  background: var(--black);
  padding: 1.875rem;
}
.apresentacao .apresentacao__call img {
  width: 4rem;
}
.apresentacao .apresentacao__call h3 {
  font-size: 2.25rem;
  line-height: 2.4375rem;
  margin-bottom: 2.25rem;
}
.apresentacao .apresentacao__info {
  padding: 1.875rem;
  text-align: center;
  place-self: center;
}
.apresentacao .apresentacao__info p {
  line-height: 1.5625rem;
  margin-top: 1.875rem;
}
.apresentacao .apresentacao__info .apresentacao__icon {
  width: 4rem;
  margin: 0 auto;
}
@media (min-width: 48.125em) {
  .apresentacao {
    grid-template-columns: repeat(3, 1fr);
  }
}

.cat_section {
  height: 29.375rem;
  background: url(../../../img/cat-blackbg.jpeg) no-repeat center;
  background-size: cover;
  color: var(--white);
  padding-top: 4.375rem;
}
.cat_section h3 {
  font-size: 2.25rem;
  margin-bottom: 1.25rem;
  max-width: 23.25rem;
}
.cat_section h4 {
  font-size: 1.25rem;
  text-transform: uppercase;
  letter-spacing: 0.625rem;
  margin-bottom: 4.375rem;
}
@media (min-width: 61.875em) {
  .cat_section {
    height: 35.625rem;
  }
}

.abrigo {
  background: var(--light-grey);
  padding: 3.75rem 1.375rem;
}
.abrigo .flex {
  display: flex;
}
@media (max-width: 48.0625em) {
  .abrigo .flex {
    flex-direction: column;
  }
}
@media (min-width: 61.875em) {
  .abrigo .flex {
    padding-left: 5.625rem;
    padding-right: 5.625rem;
  }
}
.abrigo__info {
  padding: 1.875rem 0.625rem;
  flex: 1 1 30%;
}
.abrigo__info h4 {
  font-size: 1.25rem;
  letter-spacing: 0.625rem;
  text-transform: uppercase;
  margin-bottom: 1.25rem;
}
.abrigo__info h3 {
  font-size: 2.25rem;
  margin-bottom: 1.25rem;
}
.abrigo__foto--dupla {
  flex: 1 1 40%;
}
.abrigo__foto--urso {
  padding-top: 3.75rem;
  flex: 1 1 30%;
  display: flex;
  background: var(--black);
}
.abrigo__foto--urso img {
  align-self: flex-end;
}
@media (min-width: 61.875em) {
  .abrigo {
    flex-direction: row;
  }
}

.treinamento__title {
  display: grid;
  place-content: end;
  background: var(--light-grey);
  padding: 1.875rem 0.625rem;
  text-align: right;
  flex: 1 1 339px;
}
.treinamento__title h4 {
  font-size: 1.25rem;
  letter-spacing: 0.625rem;
  text-transform: uppercase;
  margin-bottom: 1.25rem;
}
.treinamento__title h3 {
  font-size: 2.25rem;
  margin-bottom: 0;
}
@media (min-width: 48.125em) {
  .treinamento__title {
    padding: 1.875rem;
  }
}
.treinamento__img {
  flex: 1 1 383px;
}
.treinamento__img img {
  height: 30.625rem;
  object-fit: cover;
}
@media (min-width: 48.125em) {
  .treinamento__img img {
    height: 35.9375rem;
  }
}
.treinamento__info {
  display: grid;
  align-content: end;
  padding: 1.875rem 0.625rem;
  flex: 1 1 249px;
}
.treinamento__info p {
  line-height: 1.5625rem;
  margin: 0;
}
@media (min-width: 61.875em) {
  .treinamento__info p {
    max-width: 19.9375rem;
  }
}
.treinamento__info .socials {
  padding-top: 1.875rem;
  display: flex;
  gap: 1.25rem;
}
.treinamento__info .socials a {
  font-size: 1.25rem;
  display: block;
  padding: 0.3125rem;
  color: var(--black);
}
@media (min-width: 48.125em) {
  .treinamento__info {
    padding: 1.875rem;
  }
}
@media (min-width: 48.125em) {
  .treinamento {
    display: flex;
    flex-wrap: wrap;
  }
}

.services {
  background: var(--v-light-grey);
}
@media (min-width: 48.125em) {
  .services .flex {
    display: flex;
  }
}
.services__info {
  flex: 1 1 360px;
  display: grid;
  align-content: center;
  padding: 1.875rem 0.625rem;
}
.services__info h3 {
  font-size: 2.25rem;
  margin-bottom: 0.625rem;
}
.services__info h4 {
  font-size: 1.25rem;
  letter-spacing: 0.625rem;
  margin-bottom: 1.875rem;
}
.services__info p {
  line-height: 1.5625rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 48.125em) {
  .services__info p {
    max-width: 24.375rem;
  }
}
.services__info .btn {
  display: inline-block;
  padding: 0.625rem 3.125rem;
  color: var(--white);
  text-transform: uppercase;
  letter-spacing: 0.1875rem;
  font-weight: 700;
  background: var(--black);
  width: max-content;
  line-height: 1.5625rem;
}
.services__info .btn:hover {
  background: var(--med-grey);
}
@media (min-width: 48.125em) {
  .services__info {
    padding: 1.875rem;
    justify-content: end;
  }
}
@media (min-width: 61.875em) {
  .services__info {
    padding: 3.75rem;
    justify-content: end;
  }
}
.services__img {
  flex: 1 1 390px;
}

.quote {
  padding: 3.75rem 0;
  background: var(--black);
}
.quote p {
  color: var(--white);
  font-family: var(--font-playfair);
  font-size: 2.25rem;
  text-align: center;
  margin-bottom: 1.875rem;
}
.quote ul {
  display: flex;
  gap: 1.25rem;
  justify-content: center;
}
.quote ul a {
  font-size: 1.25rem;
  color: var(--white);
  display: block;
  padding: 0.3125rem;
}

.form {
  background: url(../../../img/puppie.jpg) no-repeat;
  padding: 3.75rem 0;
  background-size: cover;
  background-position: right;
}
.form .grid {
  display: grid;
}
@media (min-width: 48.125em) {
  .form .grid {
    grid-template-columns: 1fr 1fr;
  }
}
.form__info {
  text-align: right;
  grid-column: 2/-1;
}
.form__info h3 {
  font-size: 2.25rem;
  margin-bottom: 1.25rem;
}
.form__info p {
  line-height: 1.5625rem;
  margin-bottom: 1.875rem;
}
.form__input {
  display: flex;
  gap: 0.9375rem;
  flex-direction: column;
}
.form__input input {
  padding: 0.75rem;
  font-size: 1rem;
  outline: none;
}
.form__input button {
  background: var(--black);
  padding: 0.625rem 0;
  line-height: 1.5625rem;
  color: var(--white);
  cursor: pointer;
  font-size: 1rem;
  border: none;
  outline: none;
}